@media (min-width: 1024px) {
  .card-grid-wrapper {
    display: grid;
    padding-bottom: 0;
    margin: 0;
    border-bottom: none;
    overflow-wrap: break-word;
    word-break: break-word;
  }
  .headline-type-10 {
    color: #fff;
    line-height: 1.38;
    font-size: 16px;
  }
}
@media (min-width: 768px) {
  .card--image-wrapper {
    margin-bottom: 0;
    border-bottom: 0;
    padding-bottom: 0;
  }
}
.card--image-wrapper {
  display: grid;
  grid-template-columns: 4fr 8fr;
  column-gap: 1rem;
}
.image-figure {
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
}
.small-newsImage {
  width: 100%;
  height: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0;
  overflow-wrap: break-word;
  word-break: break-word;
  object-fit: cover;
  border-radius: 5px;
}
.headline-type-10 {
  font-weight: 700;
  letter-spacing: 400;
  margin: 0;
  padding: 0;
  color: #fff;
  font-size: 16px;
}
