.story-head {
  display: flex;
  flex-direction: column;
  border-bottom: 1px solid #bababa;
  margin-bottom: 3rem;
}
.headline-type-10 {
  text-align: left;
  margin-bottom: 2rem;
  color: #35144f;
  font-weight: 800;
  letter-spacing: 400;
}
.para {
  color: #6c6c6c;
  text-align: left;
}
.section-timestamp {
  margin-bottom: 2rem;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
.sec-head {
  font-size: 14px;
  font-weight: 800;
  line-height: 1.43;
  color: #35144f;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .headline-type-10 {
    font-size: 36px;
  }
  .para {
    line-height: 1.3;
    font-size: 20px;
    margin-bottom: 2rem;
  }
  .subs {
    display: block;
    margin-bottom: 4rem;
  }
  .subs-head {
    display: flex;
    flex-direction: row;
    align-items: cente;
  }
  .subs-head-text {
    position: relative;
    font-size: 25px;
    font-weight: 800;
    line-height: 1.23;
    color: #35144f;
    margin-bottom: 1rem;
    padding-right: 1rem;
  }
  .line {
    height: 5px;
    background-color: #febf04;
    flex: 1;
    margin-top: 10px;
    border-radius: 5px;
  }
  .subs-para {
    font-size: 16px;
    font-weight: 500;
    line-height: 1.38;
    color: #292929;
    margin-bottom: 2rem;
  }
  .subs-form {
    margin-bottom: 1rem;
    display: flex;
    flex-direction: row;
    width: 100%;
  }
  .inp-sec {
    position: relative;
    width: 100%;
  }
  .inp {
    height: 100%;
    width: 100%;
    font-size: 16px;
    border-radius: 5px 0 0 5px;
    border: 1px solid #bababa;
  }
  .subs-btn {
    padding: 1.5rem;
    font-size: 16px;
    font-weight: 700;
    line-height: 1.38;
    text-align: center;
    color: #fff;
    background-color: #35144f;
    border: #35144f;
    border-radius: 0 5px 5px 0;
    margin-left: -2px;
    min-width: 105px;
  }
}
.subs-para {
  font-size: 16px;
  font-weight: 500;
  line-height: 1.38;
  color: #292929;
  margin-bottom: 2rem;
}
