.infinite_scroll {
  min-height: 50px;
}
.lens-section {
  background-color: #f5f5f5;
  position: relative;
}
@media (min-width: 1024px) {
  .lens-section {
    padding: 4rem;
    margin: 100px auto 6rem;
    border-radius: 5px;
  }
  .icon_text {
    top: -55px;
    left: 0;
  }
  .icon_text svg {
    width: 180px;
    height: 70px;
  }
  .icon_text .icon__text {
    margin: -1.5rem 0 0 -120px;
    font-size: 2.5rem;
    font-style: bold;
  }
  .section-main-grid {
    display: grid;
    grid-template-columns: 7fr 5fr;
    column-gap: 4rem;
  }
  .section-col2-grid {
    row-gap: 4.5rem;
  }
}
.section-col2-grid {
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .lens-main-card {
    padding-bottom: 1.5rem;
    margin-bottom: 0;
    border-bottom: 0;
  }
  .headline-type-lense-1 {
    font-size: 20px;
  }
  .section-col2-grid {
    row-gap: 1rem;
  }
}
.section-heading {
  position: absolute;
  top: -40px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  z-index: 1;
  width: 96%;
}
.icon_text {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.icon_text svg {
  padding-bottom: 5px;
  margin-bottom: 5px;
  padding-right: 5px;
}
.icon_text .icon__text {
  color: #292929;
  line-height: 1.17;
  font-weight: 800;
}
.section-main-card {
  background-color: #fff;
  padding-bottom: 0;
}
.lens-main-card {
  display: flex;
  flex-direction: column;
  box-shadow: 0 2px 3px 0 rgb(0 0 0 / 15%);
}
.lens-main-card-wrapper {
  position: relative;
}
.lens-main-card-wrapper figure {
  background-color: #f7f7f7;
  background-size: cover;
}
.lens-main-card-wrapper figure img {
  image-rendering: auto !important;
  max-width: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 5px;
}
.headline-type-lense-1 {
  color: #292929;
  padding: 1rem 2rem;
  border-radius: 5px;
  font-weight: 700;
  line-height: 1.3;
}
.headline-icon-wrapper {
  margin-right: 1rem;
  display: inline-flex;
}
.headline-icon-wrapper svg {
  width: 20;
  height: 20;
}
.section-col2-grid {
  display: grid;
}
.lens-sec-card {
  background-color: #fff;
  display: flex;
  flex-direction: column;
  border-top: 0.5rem solid #35144f;
  border-radius: 5px;
  box-shadow: 0 1px 2px 0 rgb(0 0 0 / 15%);
  position: relative;
}
.title-wrapper {
  padding: 1rem 1.5rem;
  min-height: 0;
}
.headline-type-lense-2 {
  color: #292929;
  margin-top: 1rem;
  line-height: 1.38;
  overflow: hidden;
  font-size: 16px;
  font-weight: 700;
  letter-spacing: 400;
}
.headline-icon-wrapper-2 {
  margin-right: 0.5rem;
  display: inline-flex;
}
.headline-icon-box svg {
  width: 15;
  height: 15;
}
