.videonews-root-conatiner {
  background-color: #292929;
}
@media (min-width: 1024px) {
  .root--container {
    padding: 4rem 0;
    margin: 100px auto 6rem;
    max-width: 1110px;
  }
  .root--container-grid {
    display: grid;
    grid-template-columns: 8fr 4fr;
    grid-column-gap: 3.5rem;
    column-gap: 3.5rem;
    grid-auto-rows: 0.01fr;
  }
  .col2-2-grid {
    display: grid;
    row-gap: 1.5rem;
    overflow-y: scroll;
    border: 1px solid #e6e6e6;
    border-radius: 0.5rem;
    padding: 1rem;
    margin-top: 0;
    overflow-wrap: break-word;
    word-break: break-word;
  }
}
.root--container {
  display: flex;
  flex-direction: column;
  position: relative;
}
.root--container-grid {
  grid-row-gap: 1rem;
  row-gap: 1rem;
  margin: 0 auto;
}
.root--container-grid a {
  cursor: pointer;
}
.video-news-card {
  position: relative;
}
.card-image-figure {
  position: relative;
  /* background-color: #f7f7f7; */
  background-size: cover;
}
.card-image-figure img {
  max-width: 100%;
  margin: 0;
  padding: 0;
}
.news-image {
  /* position: absolute; */
  border-radius: 5px;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media (min-width: 768px) {
  .card-image-headline {
    position: absolute;
    opacity: 0.85;
    background-color: #fff;
    padding: 1rem;
    bottom: 35px;
    width: 78%;
    border-radius: 0 5px 5px 0;
  }
  .headline-type-5 {
    color: #292929;
    font-weight: 700;
    line-height: 1.38;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    font-size: 16px;
  }
}
.disable-scrollbar ::-webkit-scrollbar {
  background: transparent;
  width: 0px;
}
