.explainer-container {
  background-color: #f5f5f5;
  margin: 9rem auto 4rem;
  position: relative;
}
.explainers-headig-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  z-index: 1;
  align-items: center;
  background: white;
}
.headline-explainer {
  color: #35144f;
  font-weight: 800;
  display: flex;
  width: inherit;
}
@media (min-width: 768px) {
  .headline-explainer {
    font-size: 36px;
    line-height: 1.17;
  }
  .exp-grid {
    display: grid;
    grid-template-columns: 0fr 5fr 5fr 0fr;
    column-gap: 3.5rem;
    margin: 0;
  }
  .custom-story-card-10 {
    grid-column: 2;
    align-self: center;
  }
  .image-heading-card {
    margin: 0 4rem -4rem;
  }
  .pri-card {
    font-size: 50px;
  }
  .sec-card {
    font-size: 25px;
    left: 30%;
  }
  .headline-type-13 {
    line-height: 1.27;
  }
  .col__2_grid {
    grid-row-gap: 3rem;
    row-gap: 3rem;
    grid-column: 3;
  }
}
.exp-grid {
  display: grid;
  row-gap: 2rem;
  padding: 4rem 0;
}
a {
  text-decoration: none;
}
.custom-story-card-10 {
  display: flex;
  flex-direction: column;
}
.image-heading-card {
  z-index: 1;
  background-color: #35144f;
  border-radius: 1rem;
  padding: 1rem;
  position: relative;
  transform: rotate(-5deg);
}
.pri-card {
  color: #fff;
  font-weight: 700;
  text-align: center;
}
.sec-card {
  background-color: #febf04;
  line-height: 1.23;
  color: #35144f;
  padding: 7px 2rem;
  border-radius: 5px;
  position: absolute;
  left: 25%;
  bottom: -27px;
  font-weight: bold;
  text-align: center;
}
.custom-story-card-wrapper {
  overflow: hidden;
}
.card-round-image {
  display: inline-block;
  border-radius: 50%;
  overflow: hidden;
}
.card-image-big {
  display: inline-block;
  width: 500px;
  height: 500px;
  max-width: 100%;
  object-fit: cover;
  /* overflow: hidden; */
}
.custom-story-card-header {
  margin-top: -4rem;
  z-index: 1;
  background-color: white;
}
.headline-type-13 {
  color: #292929;
  font-weight: 700;
  text-align: center;
  background-color: #fff;
  padding: 1.5rem;
  font-size: 21px;
}
.col__2_grid {
  display: grid;
}
.small-card-grid {
  display: grid;
  grid-template-columns: 2fr 3fr;
  align-items: center;
}
.card-image-small {
  display: inline-block;
  width: 200px;
  height: 200px;
  max-width: 100%;
  object-fit: cover;
  /* overflow: hidden; */
}
.custom-story-card-header-small {
  align-self: center;
  margin-left: -2rem;
}
.headline-type--14 {
  color: #292929;
  font-weight: bold;
  align-self: center;
  background-color: #fff;
  padding: 1.5rem 1.5rem 1.5rem 3rem;
  font-size: 20px;
  /* line-height: 1.27; */
}
